
import { ComponentProps, ComponentRef } from '@ionic/core';
import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { UntypedFormBuilder, UntypedFormGroup, Validators } from '@angular/forms';
import { ModalController } from '@ionic/angular';

import { MenuItem } from 'primeng/api';
import { Subscription } from 'rxjs';
import { Dropdown } from 'primeng/dropdown';

import { CondominioDto } from '../../../interfaces/condominios/condominio.dto';
import { CondominioService } from '../../../services/condominio/condominio.service';
import { PageOrder } from './../../../constants/page.constant';
import { PageableDto } from './../../../interfaces/others/pageable.dto';
import { Role } from './../../../constants/role.constants';
import { ToastMessageService } from './../../../services/toast/toast-message.service';
import { AddResponsavelDto } from './../../../interfaces/dependentes/dependente.dto';
import { user_pefil_id } from './../../../constants/system.constant';
import { UserService } from './../../../services/user/user.service';

@Component({
  selector: 'app-cliente-cadastro',
  templateUrl: './cliente-cadastro.component.html',
  styleUrls: ['./cliente-cadastro.component.scss'],
})
export class ClienteCadastroComponent implements OnInit, OnDestroy {

  @Input()
  responsavel_id: string;

  form: UntypedFormGroup;

  condominios: PageableDto<CondominioDto>;
  selectedCondominio: string;
  currentCondominiosPage = 1;
  searchedCondominio = '';
  isLoaded = false;

  steps: MenuItem[];
  stepIndex = 0;

  createdUserId = '';
  userWasCreated = false;

  isCliente = false;

  maxDate: Date = new Date();
  private subscriptions = new Subscription();

  constructor(
    private readonly formBuilder: UntypedFormBuilder,
    private readonly userService: UserService,
    private readonly condominioService: CondominioService,
    private readonly modalCadastro: ModalController,
    private readonly toastService: ToastMessageService,

  ) {
    this.form = this.formBuilder.group({
      nome: [, [Validators.required]],
      email: [, [Validators.required, Validators.email]],
      cpf: [, []],
      condominio_id: [, [,]],

      identidade: [, [ Validators.minLength(7), Validators.maxLength(11)]],
      tel1: [, [Validators.required,]],
      tel2: [, []],
      data_nascimento: [, [Validators.required,]],
      grau_parentesco: [, []],
    });
  }

  ngOnInit() {
    this.steps = [
      {
        label: 'Informações',
      },
      {
        label: 'Endereço',
      },
    ];

    if (this.responsavel_id) {
      this.form.get('grau_parentesco').setValidators([Validators.required]);
      this.form.get('grau_parentesco').updateValueAndValidity();
    }
    // this.loadCondominios();
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }



  loadCondominios(page = 1, dropdown?: Dropdown) {
    const sub = this.condominioService.list(
      { searchedCondominio: this.searchedCondominio, },
      { order: PageOrder.ASC, page, take: 10 })
      .subscribe(data => {
        this.condominios = data;
        if (dropdown) {
          dropdown.show();
        }
      });

    this.subscriptions.add(sub);
  }
  searchCondominio(dropdown?: Dropdown) {
    this.loadCondominios(1, dropdown);
  }
  paginarCondominios(event, dropdown?: Dropdown) {
    const { first, rows } = event;
    const page = Number((Number(first) / Number(rows)) + 1);
    if (page != this.currentCondominiosPage) {
      this.currentCondominiosPage = page;
      this.loadCondominios(page, dropdown);
    }
  }


  create() {
    const nome = this.form.get('nome').value;
    const email = this.form.get('email').value;
    const cpf = this.form.get('cpf').value;
    //const { id: condominio_id } = this.form.get('condominio_id').value;

    const identidade = this.form.get('identidade').value;
    const tel1 = this.form.get('tel1').value;
    const tel2 = this.form.get('tel2').value;
    const data_nascimento = this.form.get('data_nascimento').value;


    const responsavel: AddResponsavelDto = this.responsavel_id ? {
      grau_parentesco: this.form.get('grau_parentesco').value,
      responsavel_id: this.responsavel_id,
    } : undefined;

    const sub = this.userService.createUser({
      nome, perfil_id: user_pefil_id, identidade, tel1, tel2, data_nascimento, email, cpf, responsavel
    })
      .subscribe(response => {
        this.createdUserId = response.id;
        this.userWasCreated = true;
        this.fechar();
      });
    this.subscriptions.add(sub);
  }

  changePerfil(event) {
    this.form.get('cargo_id').setValue(undefined);
    const nome = this.form.get('perfil_id').value?.nome || undefined;

    if (nome == Role.CLIENTE) {
      this.isCliente = true;
      return;
    }

    this.isCliente = false;
  }

  listenEndereco(event) {
    this.fechar(this.userWasCreated);
  }

  fechar(userWasCreated = false) {
    this.modalCadastro.dismiss(this.userWasCreated);
  }

  isInputError(inputName: string): boolean {
    const resp =
      !this.form.controls[inputName].untouched &&
      this.form.controls[inputName].errors;

    if (resp) {
      return true;
    }
    return false;
  }

  disableCreateBtn(): boolean {
    const situacao = !this.form.valid;

    return situacao;
  }

  private async showModal(
    component: ComponentRef,
    componentProps?: ComponentProps
  ) {
    const modal = await this.modalCadastro.create({
      component,
      backdropDismiss: false,
      cssClass: 'modal-size-100',
      componentProps,
    });

    modal.onDidDismiss().then((data) => {
    });

    return await modal.present();
  }
}
