import { MenuItem } from 'primeng/api';
import { Subscription } from 'rxjs';
import { EChartsOption } from 'echarts';

import { PageOrder } from './../../../../shared/constants/page.constant';
import { BasicNotificacaoDto } from './../../../../shared/interfaces/others/notificacao.dto';
import { PageableDto } from './../../../../shared/interfaces/others/pageable.dto';
import { UserDto } from './../../../../shared/interfaces/users/user.dto';
import { StorageService } from './../../../../shared/services/auth/storage.service';
import { NotificacaoService } from './../../../../shared/services/notificacao/notificacao.service';
import { UserService } from './../../../../shared/services/user/user.service';
import { NotificacaoUtils } from './../../../../shared/utils/NotificacaoUtils';
import { Role } from './../../../../shared/constants/role.constants';
import { ToastMessageService } from './../../../../shared/services/toast/toast-message.service';
import { NotificacaoStatus } from './../../../../shared/constants/notificacao.constant';
import { FuncionarioProfileComponent } from './../../../../shared/components/adm-recurso-module/funcionarios/funcionario-profile/funcionario-profile.component';
import { SimpleDashboardDto } from './../../../../shared/interfaces/adm-financeiras/dashboards/dashboard.dto';
import { ContasColaboradorService } from './../../../../shared/services/adm-financeira/contas/contas-colaborador/contas-colaborador.service';
import { TesourariasEmpresaService } from './../../../../shared/services/adm-financeira/tesourarias-empresa/tesourarias-empresa.service';
import { TesourariasService } from './../../../../shared/services/adm-financeira/tesourarias/tesourarias.service';
import { PreferencesService } from './../../../../shared/services/preferences/preferences.service';
import { DashboardUtilsService } from './../../../../shared/services/utils/dashboard-utils.service';
import { EmailService } from './../../../../shared/services/comunicacao/emails/email.service';
import { ToastEnum, ToastPrimeSeverityEnum } from './../../../../shared/constants/toast.constant';
import { ContasClienteService } from './../../../services/adm-financeira/contas/contas-cliente/contas-cliente.service';

@Component({
  selector: 'app-cliente-profile',
  templateUrl: './cliente-profile.component.html',
  styleUrls: ['./cliente-profile.component.scss'],
})
export class ClienteProfileComponent implements OnInit, OnDestroy {

  @Input()
  user: UserDto;


  selectedDate: Date = new Date();
  selectedOption = false;

  simplesDash: SimpleDashboardDto;
  inAndOutDash: EChartsOption;

  hasAccount = false;

  private subscriptions = new Subscription();
  constructor(
    private readonly toastService: ToastMessageService,
    private readonly modalCtrl: ModalController,
    private readonly contasClienteService: ContasClienteService,
    private readonly dashboardUtilsService: DashboardUtilsService,
    private readonly preferencesService: PreferencesService,
  ) { }

  ngOnInit() {
    this.loadMiniDash();
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  loadMiniDash() {
    const { id } = this.user;
    const sub = this.contasClienteService.findByColaboradorAndDate(id, this.selectedDate)
      .subscribe({
        next: (response) => {
          this.simplesDash = this.dashboardUtilsService.convertToSimpleDashCliente(response);
          this.inAndOutDash = this.dashboardUtilsService.convertToInOutSimpleDashCliente(response);
          this.hasAccount = true;
        },
        complete: () => { this.toastService.clearToast() }
      });
    this.subscriptions.add(sub);
  }

  changeDate() {
    this.simplesDash = undefined;
    this.inAndOutDash = undefined;
    this.hasAccount = false;
    this.selectedOption = false;

    this.loadMiniDash();
  }

  getTheme() {
    const { theme, dashboard } = this.preferencesService.getDashboardTheme(this.inAndOutDash);
    this.inAndOutDash = dashboard;
    return theme;
  }

  setUserImage(user: UserDto) {
    return user.avatar_url || "assets/imgs/outros/default-user.jpg";
  }

  goToPedidos() {
    this.selectedOption = true;
    //this.showModal(ClienteHistoricoComprasComponent, { cliente_id: this.user.id });
  }

  fechar(userWasCreated = false) {
    this.modalCtrl.dismiss(userWasCreated);
  }


  private async showModal(
    component: ComponentRef,
    componentProps?: ComponentProps
  ) {
    const modal = await this.modalCtrl.create({
      component,
      backdropDismiss: false,
      cssClass: 'modal-size-100',
      componentProps,
    });

    modal.onDidDismiss().then((data) => {
      const { data: hasUpdate } = data;
      if (hasUpdate) {
        this.toastService.presentToast({
          titulo: 'Sucesso',
          detalhe: 'Operação bem sucedida!',
          duracao: ToastEnum.mediumDuration,
          gravidade: ToastPrimeSeverityEnum.SUCESSO
        });
      }
    });

    return await modal.present();
  }
}
