
import { Component, OnDestroy, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { ComponentProps, ComponentRef } from '@ionic/core';

import { MenuItem, MessageService, SelectItem } from 'primeng/api';
import { Dropdown } from 'primeng/dropdown';
import { Subscription } from 'rxjs';

import { ToastEnum, ToastPrimeSeverityEnum } from '../../../../shared/constants/toast.constant';
import { TipoUsuarioEnum } from './../../../constants/tipo-user.constant';
import { StatusCliente, StatusUser } from './../../../constants/status.constant';
import { ToastMessageService } from './../../../services/toast/toast-message.service';
import { PageableDto } from './../../../interfaces/others/pageable.dto';
import { PageOrder } from './../../../constants/page.constant';

import { UserDto } from '../../../../shared/interfaces/users/user.dto';
import { CondominioService } from '../../../services/condominio/condominio.service';
import { UserService } from '../../../../shared/services/user/user.service';
import { EnderecoComponent } from '../../endereco-module/endereco/endereco.component';
import { UpdateEnderecoComponent } from '../../endereco-module/update-endereco/update-endereco.component';
import { AuthorizationMainComponent } from '../../authorization-module/authorization-main/authorization-main.component';
import { ClienteCadastroComponent } from '../cliente-cadastro/cliente-cadastro.component';
import { ClienteDetailComponent } from '../cliente-detail/cliente-detail.component';
import { ClienteProfileComponent } from '../cliente-profile/cliente-profile.component';
import { DependenteViewComponent } from '../../adm-dependentes-module/dependente-view/dependente-view.component';
import { PetViewComponent } from '../../adm-pet-module/pet-view/pet-view.component';
import { VeiculoViewComponent } from '../../adm-veiculo-module/veiculo-view/veiculo-view.component';

@Component({
  selector: 'app-cliente-main',
  templateUrl: './cliente-main.component.html',
  styleUrls: ['./cliente-main.component.scss'],
})
export class ClienteMainComponent implements OnInit, OnDestroy {

  users: PageableDto<UserDto>;
  sortOptions: SelectItem[] = [];
  sortCondominios: SelectItem[] = [];

  isLoaded = false;
  currentCondominioLoaded: string;
  searchedNome = '';
  selectedCondominio = '';
  selectedStatus = '';
  currentPage = 1;

  selectedUser: UserDto;

  editMenuItens: MenuItem[] = [{
    label: 'Opções',
    items: [
      {
        label: 'Atualizar',
        icon: 'pi pi-user-edit',
        command: () => this.detalharAtualizar(this.selectedUser),
      },
      {
        label: 'Dependentes',
        icon: 'pi pi-users',
        command: () => this.detalharDependentes(this.selectedUser),
      },
      {
        label: 'Pets',
        icon: 'pi pi-heart',
        command: () => this.detalharPets(this.selectedUser),
      },
      {
        label: 'Veículos',
        icon: 'pi pi-car',
        command: () => this.detalharVeiculos(this.selectedUser),
      },
      /* {
         label: 'Comunicação',
         icon: 'pi pi-megaphone',
         command: () => this.detalharComunicacao(this.selectedUser),
       }*/
    ]
  }
  ];

  private subscriptions = new Subscription();

  constructor(
    private readonly userService: UserService,
    private readonly modal: ModalController,
    private readonly toastService: ToastMessageService,
    private readonly CondominioService: CondominioService,
  ) { }

  ngOnInit() {
    //Permitir filtrar usuário por status
    this.sortOptions = [...StatusCliente];

    // this.loadCondominio();
    this.loadClientes();

  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  changeFiltro({ value }) {
    this.selectedStatus = value;
    this.loadClientes(1);
  }

  changeCondominio({ value }) {
    this.selectedCondominio = value;
    this.loadClientes(1);
  }

  clearFilter() {
    this.loadClientes(1);
  }

  clearAllFilters(event, dropdown_Condominios: Dropdown, dropdown_filtro: Dropdown) {
    this.searchedNome = undefined;
    this.selectedCondominio = undefined;
    this.selectedStatus = undefined;

    dropdown_Condominios.writeValue(null);
    dropdown_filtro.writeValue(null);

    this.loadClientes(1);
  }

  loadCondominio() {
    const sub = this.CondominioService.list()
      .subscribe(page => {
        this.sortCondominios = page.data.map((l) => {
          const { id, nome, codigo } = l;
          return {
            value: id,
            label: `[${codigo}] - ${nome}`
          }
        });
      });
    this.subscriptions.add(sub);
  }

  loadClientes(page = 1) {
    const sub = this.userService.listUsers({
      searchedUser: this.searchedNome,
      condominio_id: this.selectedCondominio,
      status: this.selectedStatus,
      tipo_usuario: TipoUsuarioEnum.USUARIO,
    },
      { order: PageOrder.DESC, page, take: 5 })
      .subscribe(response => {
        this.users = response;
        this.isLoaded = true;
      });
    this.subscriptions.add(sub);


  }

  detalharAtualizar(user: UserDto) {
    this.showModal(ClienteDetailComponent, { user });
  }

  detalharDependentes(responsavel: UserDto) {

    this.showModal(DependenteViewComponent, { responsavel });
  }

  detalharPets(responsavel: UserDto) {
    this.showModal(PetViewComponent, { responsavel });
  }

  detalharVeiculos(responsavel: UserDto) {
    this.showModal(VeiculoViewComponent, { responsavel });
  }

  detalharComunicacao(responsavel: UserDto) {
    this.showModal(AuthorizationMainComponent, { responsavel });
  }

  detalharEndereco(user: UserDto) {
    if (user.endereco) {
      this.showModal(UpdateEnderecoComponent, { currentEndereco: user, isCliente: true });
    } else {
      this.showModal(EnderecoComponent, { idOf: user.id, isCliente: true, isModal: true });
    }
  }

  setUserImage(user: UserDto) {
    return user.avatar_url || "assets/imgs/outros/default-user.jpg";
  }

  createNewUser() {
    this.showModal(ClienteCadastroComponent);
  }

  search() {
    this.loadClientes(1);
  }

  paginar(event) {
    const { first, rows } = event;
    const page = Number((Number(first) / Number(rows)) + 1);
    if (page != this.currentPage) {
      this.currentPage = page;
      this.loadClientes(page);
    }
  }

  private async showModal(
    component: ComponentRef,
    componentProps?: ComponentProps
  ) {
    const modal = await this.modal.create({
      component,
      backdropDismiss: false,
      cssClass: 'modal-size-100',
      componentProps,
    });

    modal.onDidDismiss().then((data) => {
      const { data: hasUpdate } = data;
      this.isLoaded = false;
      this.loadClientes();
      if (hasUpdate) {
        this.toastService.presentToast({
          titulo: 'Sucesso',
          detalhe: 'Operação bem sucedida!',
          duracao: ToastEnum.mediumDuration,
          gravidade: ToastPrimeSeverityEnum.SUCESSO
        });
      }
    });

    return await modal.present();
  }
}
