import { VeiculosCoresOptions, VeiculosTipoOptions } from './../../../constants/veiculos.constant';
import { ComponentRef, ComponentProps } from '@ionic/core';

import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { UntypedFormBuilder, UntypedFormGroup, Validators } from '@angular/forms';

import { Dropdown } from 'primeng/dropdown';
import { Subscription } from 'rxjs';
import { ConfirmationService, SelectItem } from 'primeng/api';
import { Table } from 'primeng/table';

import { UserService } from './../../../services/user/user.service';
import { ToastMessageService } from './../../../../shared/services/toast/toast-message.service';
import { UserDto, } from './../../../interfaces/users/user.dto';
import { DependenteDto } from "./../../../interfaces/dependentes/dependente.dto";
import { ToastEnum, ToastPrimeSeverityEnum } from './../../../constants/toast.constant';
import { PageOrder } from './../../../constants/page.constant';
import { TipoUsuarioEnum } from './../../../constants/tipo-user.constant';
import { PageableDto } from './../../../interfaces/others/pageable.dto';
import { VeiculosService } from './../../../services/veiculos/veiculos.service';
import { VeiculoDto } from './../../../interfaces/veiculos/veiculo.dto';
import { EspeciesOptions } from './../../../constants/especie.constant';

@Component({
  selector: 'app-veiculo-view',
  templateUrl: './veiculo-view.component.html',
  styleUrls: ['./veiculo-view.component.scss'],
})
export class VeiculoViewComponent implements OnInit, OnDestroy {

  @Input()
  responsavel: UserDto;

  veiculos: PageableDto<VeiculoDto>;

  shouldUpdate = false;

  form: UntypedFormGroup;

  showDialog = false;
  currentPage = 1;
  pageOrder = PageOrder.DESC;
  currentTake = 5;

  veiculosCoresOptions: SelectItem[] = VeiculosCoresOptions;
  veiculosTipoOptions: SelectItem[] = VeiculosTipoOptions;

  private subscriptions = new Subscription();

  constructor(
    private readonly formBuilder: UntypedFormBuilder,
    private readonly userService: UserService,
    private readonly modalVeiculo: ModalController,
    private readonly toastService: ToastMessageService,
    private readonly confirmationService: ConfirmationService,
    private readonly veiculosService: VeiculosService,
  ) {
    // Implementar o Update
  }

  ngOnInit() {
    this.form = this.formBuilder.group({
      placa: [, [Validators.required, Validators.minLength(7), Validators.maxLength(7)]],
      cor: [, [Validators.required,]],
      fabricante: [, [Validators.required,]],
      modelo: [, [Validators.required,]],
      ano: [, [Validators.required,]],
      tipo: [, [Validators.required,]],
    });

    this.loadVeiculos();
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  loadVeiculos(page = 1, dt?: Table) {
    const sub = this.veiculosService.list(
      { responsavel_id: this.responsavel.id },
      { order: this.pageOrder, page, take: this.currentTake }
    ).subscribe({
      next: response => {
        this.veiculos = response;
        this.veiculos.data.forEach(v => {
          v.ano_date = new Date(v.ano, 0, 1);
        });
      },
      complete: () => {
        if (dt) {
          dt.paginator = true;
        }
      }
    });
    this.subscriptions.add(sub);
  }

  paginar(event, dt: Table) {
    dt.paginator = false;
    const { first, rows, sortOrder } = event;
    const page = Number((Number(first) / Number(rows)) + 1);

    this.currentPage = page;
    this.currentTake = rows;
    this.pageOrder = sortOrder && sortOrder == 1 ? PageOrder.DESC : PageOrder.ASC;

    this.loadVeiculos(page, dt);
  }

  create() {
    const placa = this.form.get('placa').value;
    const cor = this.form.get('cor').value;
    const fabricante = this.form.get('fabricante').value;
    const modelo = this.form.get('modelo').value;
    const ano = this.form.get('ano').value as Date;
    const tipo = this.form.get('tipo').value;

    const sub = this.veiculosService.create({
      placa, cor, fabricante, modelo, ano: ano.getFullYear(), tipo, responsavel_id: this.responsavel.id,
    })
      .subscribe(response => {
        this.loadVeiculos();
        this.toastService.presentToast({
          titulo: 'Sucesso',
          detalhe: 'Operação bem sucedida!',
          duracao: ToastEnum.shortDuration,
          gravidade: ToastPrimeSeverityEnum.SUCESSO
        });
        this.form.reset();
      });
    this.subscriptions.add(sub);

  }

  update(veiculo: VeiculoDto) {
    const { id, fabricante, modelo, cor, placa, tipo, ano_date } = veiculo;

    const ano = ano_date.getFullYear();

    const sub = this.veiculosService.update(id, {
      ano, fabricante, modelo, cor, placa, tipo,
    })
      .subscribe(response => {
        this.toastService.presentToast({
          titulo: 'Sucesso',
          detalhe: 'Operação bem sucedida!',
          duracao: ToastEnum.shortDuration,
          gravidade: ToastPrimeSeverityEnum.SUCESSO
        });
      });
    this.subscriptions.add(sub);
  }

  delete(veiculo: VeiculoDto) {
    this.confirmationService.confirm({
      message: `Deseja mesmo remover o veículo "${veiculo.placa}" ?`,
      header: `Remoção de Veículo`,
      icon: 'pi pi-exclamation-triangle',
      accept: () => {
        const sub = this.veiculosService.delete(
          veiculo.id
        )
          .subscribe(response => {
            this.loadVeiculos();
            this.toastService.presentToast({
              titulo: `Sucesso!`,
              detalhe: `Veiculo removido com sucesso.`,
              duracao: ToastEnum.shortDuration,
              gravidade: ToastPrimeSeverityEnum.SUCESSO,
            });
            this.form.reset();
          });
        this.subscriptions.add(sub);
      },
      reject: (type) => {
        this.toastService.clearToast();
      }
    });
  }

  fechar() {
    this.modalVeiculo.dismiss(this.shouldUpdate);
  }


  isInputError(inputName: string): boolean {
    const resp =
      !this.form.controls[inputName].untouched &&
      this.form.controls[inputName].errors;

    if (resp) {
      return true;
    }
    return false;
  }

  disableCreateBtn(): boolean {
    const situacao = !this.form.valid;

    return situacao;
  }

}
