import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

import { API_CONFIG } from '../../../../config/api.config';
import { CreatePetDto, PetDto, SearchPetDto, UpdatePetDto } from '../../interfaces/pets/pet.dto';
import { PageableDto, PageOptionsDto } from '../../interfaces/others/pageable.dto';
import { PageUtils } from '../../utils/PageUtils';

@Injectable({
  providedIn: 'root'
})
export class PetService {

  constructor(
    private http: HttpClient,
  ) { }

  list(filterDto?: SearchPetDto, pageOptions = new PageOptionsDto()): Observable<PageableDto<PetDto>> {
    let params = PageUtils.getPageOptionsParams(pageOptions);

    if (filterDto) {
      if (filterDto.searched_pet) {
        params = params.append('searched_pet', filterDto.searched_pet);
      }
      if (filterDto.especie) {
        params = params.append('especie', filterDto.especie);
      }
      if (filterDto.nome) {
        params = params.append('nome', filterDto.nome);
      }
      if (filterDto.responsavel_id) {
        params = params.append('responsavel_id', filterDto.responsavel_id);
      }
    }

    return this.http.get<PageableDto<PetDto>>(`${API_CONFIG.baseURL}/pets`, {
      responseType: 'json',
      params,
    });
  }

  create(newPet: CreatePetDto): Observable<PetDto> {
    return this.http.post<PetDto>(
      `${API_CONFIG.baseURL}/pets`,
      newPet,
      {
        responseType: 'json',
      });
  }

  update(pet_id: string, dto: UpdatePetDto): Observable<PetDto> {
    console.log(dto);
    
    return this.http.put<PetDto>(
      `${API_CONFIG.baseURL}/pets/${pet_id}`,
      dto,
      {
        responseType: 'json',
      });
  }

  delete(pet_id: string): Observable<any> {
    return this.http.delete<any>(
      `${API_CONFIG.baseURL}/pets/${pet_id}`,
      {
        responseType: 'json',
      });
  }
}
