import { ComponentRef, ComponentProps } from '@ionic/core';


import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { UntypedFormBuilder, UntypedFormGroup, Validators } from '@angular/forms';

import { Dropdown } from 'primeng/dropdown';
import { Subscription } from 'rxjs';
import { ConfirmationService } from 'primeng/api';

import { UserService } from './../../../services/user/user.service';
import { ToastMessageService } from './../../../../shared/services/toast/toast-message.service';
import { UserDto, } from './../../../interfaces/users/user.dto';
import { DependenteDto } from "./../../../interfaces/dependentes/dependente.dto";
import { ToastEnum, ToastPrimeSeverityEnum } from './../../../constants/toast.constant';
import { PageOrder } from './../../../constants/page.constant';
import { TipoUsuarioEnum } from './../../../constants/tipo-user.constant';
import { PageableDto } from './../../../interfaces/others/pageable.dto';
import { ClienteCadastroComponent } from '../../adm-cliente-module/cliente-cadastro/cliente-cadastro.component';

@Component({
  selector: 'app-dependente-view',
  templateUrl: './dependente-view.component.html',
  styleUrls: ['./dependente-view.component.scss'],
})
export class DependenteViewComponent implements OnInit, OnDestroy {
  
  @Input()
  responsavel: UserDto;

  dependentes: DependenteDto[];

  shouldUpdate = false;

  users: PageableDto<UserDto>;
  searchedUser = '';
  selectedUser: string;
  grau_parentesco: string = '';

  form: UntypedFormGroup;

  showDialog = false;
  selectedDependente: UserDto;

  private subscriptions = new Subscription();

  constructor(
    private readonly formBuilder: UntypedFormBuilder,
    private readonly userService: UserService,
    private readonly modalDependente: ModalController,
    private readonly modalNovoUsuario: ModalController,
    private readonly toastService: ToastMessageService,
    private readonly confirmationService: ConfirmationService,
  ) {
  }

  ngOnInit() {
    this.dependentes = this.responsavel.dependentes;
    this.loadUsers();

    this.form = this.formBuilder.group({
      grau_parentesco: [, [Validators.required, Validators.minLength(3)]],
      dependente: [, [Validators.required,]],
    });
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  findByResponsavelId() {
    const sub = this.userService.findById(this.responsavel.id
    )
      .subscribe(response => {
        this.dependentes = response.dependentes;
      });
    this.subscriptions.add(sub);
  }
  //Melhorar o filtro criando um personalizado
  //#region Usuário
  loadUsers(page = 1) {
    const sub = this.userService.listUsers(
      { searchedUser: this.searchedUser, tipo_usuario: TipoUsuarioEnum.USUARIO },
      { order: PageOrder.DESC, page, take: 10 }
    )
      .subscribe(response => {
        this.users = response;
      });
    this.subscriptions.add(sub);
  }
  paginarUser({value}) {
    this.searchedUser = undefined;
    if(value){
      this.searchedUser = value;
    }
    this.loadUsers();
  }
  //#endregion


  //#region Dependentes
  addDependente() {
    const dependente_id = this.form.get('dependente').value.id;
    const grau_parentesco = this.form.get('grau_parentesco').value;

    const sub = this.userService.addDependente(
      this.responsavel.id,
      {
        dependente_id,
        grau_parentesco,
      }
    )
      .subscribe(response => {
        this.dependentes = response.dependentes;
        this.shouldUpdate = true;
        this.toastService.presentToast({
          titulo: `Sucesso!`,
          detalhe: `Dependente adicionado com sucesso.`,
          duracao: ToastEnum.mediumDuration,
          gravidade: ToastPrimeSeverityEnum.SUCESSO,
        });
        this.form.reset();
      });
    this.subscriptions.add(sub);
  }

  removeDependente(dto: DependenteDto) {
    const { user_dependente } = dto;

    this.confirmationService.confirm({
      message: `Deseja mesmo remover ${user_dependente.nome} como dependente?`,
      header: `Remoção de dependente`,
      icon: 'pi pi-exclamation-triangle',
      accept: () => {
        const sub = this.userService.removeDependente(
          this.responsavel.id,
          user_dependente.id,
        )
          .subscribe(response => {
            this.dependentes = response.dependentes;
            this.shouldUpdate = true;
            this.toastService.presentToast({
              titulo: `Sucesso!`,
              detalhe: `Dependente removido com sucesso.`,
              duracao: ToastEnum.mediumDuration,
              gravidade: ToastPrimeSeverityEnum.SUCESSO,
            });
            this.form.reset();
          });
        this.subscriptions.add(sub);
      },
      reject: (type) => {
        this.toastService.clearToast();
      }
    });

  }

  detailDependente(dto: DependenteDto) {
    const { user_dependente } = dto;
    this.selectedDependente = user_dependente;
    this.showDialog = true;
  }

  newDependente() {
    this.showModal(ClienteCadastroComponent, { responsavel_id: this.responsavel.id });
  }

  //#endregion

  detailUser() {
    this.selectedDependente = this.form.get('dependente').value;
    if (!this.selectedDependente) {
      this.toastService.presentToast({
        detalhe: `Selecione um usuário primeiro`,
        titulo: `Seleção necessária`,
        duracao: ToastEnum.shortDuration,
        gravidade: ToastPrimeSeverityEnum.INFORMACAO,
      });
      return;
    }
    this.showDialog = true;
  }

  isInputError(inputName: string): boolean {
    const resp =
      !this.form.controls[inputName].untouched &&
      this.form.controls[inputName].errors;

    if (resp) {
      return true;
    }
    return false;
  }

  disableAddDependenteBtn(): boolean {
    const situacao = !this.form.valid;

    return situacao;
  }

  hasDependente() {
    return this.dependentes && this.dependentes?.length >= 1;
  }

  fechar() {
    this.modalDependente.dismiss(this.shouldUpdate);
  }

  private async showModal(
    component: ComponentRef,
    componentProps?: ComponentProps
  ) {
    const modal = await this.modalNovoUsuario.create({
      component,
      backdropDismiss: false,
      cssClass: 'modal-size-100',
      componentProps,
    });

    modal.onDidDismiss().then((data) => {
      const { data: hasUpdate } = data;
      console.log(`Update: ${hasUpdate}`);
      if (hasUpdate) {
        this.findByResponsavelId();
      }
    });

    return await modal.present();
  }

}
